import { userLogin } from "../libs/auth.js";
export default {
  data() {
    return {};
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  onLoad() {
    console.log("lonad");
  },
  methods: {
    async onGetAuthorize() {
      // 授权成功后的回调
      let data = await userLogin();
      this.$emit("authSuccess", data);
    },
    onAuthError() {
      uni.showToast({
		  title:"授权失败，重新授权"
	  });
    }
  }
};
